.home-wrapper {
    width: 100vw;

    .home-logo {
        width: 100%;
        height: calc(100vh - 80px);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        img {
            position: relative;
            bottom: 60px;
            max-width: 450px;
            max-height: 450px;
            transform: scale(1.2);
        }

        h1 {
            font-size: 3rem;
            color: lightblue
        }
    }
}
